import React from 'react';
import TypedReactDemo from './typed';

const Main = (props) => {
    return(
        <section id="main">
            <a id ="home"></a>
            <div className="bg-fullscreen"></div>
            <div className="bg-dark-layer"></div>

            <div className="container text-center main-padded">
                <div class="row" style={styles.row}>
                    <div class="col s12 m6">
                        <h1 className="mainTitle wow slideInDown">Katie</h1>
                        <p className="head-title-typed">I create <TypedReactDemo
                        strings={[
                            'Landing Pages','Web Applications', 
                            'Wordpress Themes','Websites', 
                            'Web Games','Code Tests', 
                            'Wireframes', 
                            'UI Designs'
                        ]}/></p>
                        <a className="btn btn-custom" style={styles.button} onClick={props.clickedAbout}>Learn More</a>
                    </div>
                    <div class="col s12 m6">
                        <img src={require('../img/katie.jpg')} style={styles.img} className="wow bounceInRight" />
                    </div>
                </div>  
            </div>

        </section>
    )
}
const styles = {
    button: {
        marginTop: '5%'
    },
    img: {
        width: '80%',
        border: '0.05px solid grey',
        borderRadius: '50%',
        boxShadow: '1px 1px 18px black'
    },
    row:{
        padding: '0 15%'
    }
}
export default Main;


