import React from 'react';

const Footer = ()=> {
    return(
        <section id="footer" style={styles.footer}>
            <div className="mediaicons">
            <div className="container">
                <div className="row">
                    <div className="col s3 m3"><a href="https://facebook.com/april.del.7" target="_blank"><i className="fa fa-facebook fa-3x"  aria-hidden="true"></i></a></div>
                    <div className="col s3 m3"><a href="https://twitter.com/kayleos" target="_blank"><i className="fa fa-twitter fa-3x"  aria-hidden="true"></i></a></div>
                    <div className="col s3 m3"><a href="https://instagram.com/kayleoss" target="_blank"><i className="fa fa-instagram fa-3x"  aria-hidden="true"></i></a></div>
                    <div className="col s3 m3"><a href="https://github.com/kayleoss" target="_blank"><i className="fa fa-github fa-3x"   aria-hidden="true"></i></a></div>
                </div>
            </div>
            </div>
            <p style={styles.p}>Made With ♥ By Katie Liu &copy; 2017</p>
        </section>
    )
}
const styles = {
    footer: {
        // background: 'url(https://www.basbleu.org/wp-content/uploads/white-brick-background.jpg)',
        backgroundSize: 'cover',
        padding: '3% 5%',
        textAlign: 'center',
        background: 'rgb(0, 183, 255)' ,
        background: '-webkit-linear-gradient(left top,rgb(2, 95, 218),rgb(0, 183, 255) )',
        background: '-o-linear-gradient(bottom right,rgb(2, 95, 218),rgb(0, 183, 255))',
        background: '-moz-linear-gradient(bottom right,rgb(2, 95, 218),rgb(0, 183, 255))',
        background: 'linear-gradient(to bottom right,rgb(2, 95, 218),rgb(0, 183, 255))'     
    },
    p: {
        fontWeight: 'bold',
        color: 'white',
        fontSize: '1em',
        marginTop: '2%'
    }
}
export default Footer;


