import React from 'react';

const Contact = ()=> {
    return(
        <section id="contact" className="wow bounceInRight">
            <h1 className="h1-white">Get In Touch</h1>
            <form action="https://formspree.io/duo.liu@ryerson.ca" method="post" style={styles.form}>
                <input type="text" name="name" placeholder="Your Name" required></input>
                <input type="text" name="company" placeholder="Your Company (Optional)"></input>
                <input type="tel" name="number" placeholder="Your Phone #" required></input>
                <textarea style={styles.textarea} name="message" placeholder="Your Message" required></textarea>
                <div style={styles.div}>
                    <button type="submit" className="btn btn-custom">Send</button>
                </div>
            </form>
        </section>
    )
}
const styles = {
    textarea: {
        marginTop: '5%',
        height: '30vh'
    },
    form: {
        padding: '0 3%',
        color: 'white'
    },
    div:{
        textAlign:'center',
        margin: '3% 0'
    }
}
export default Contact;


