import React from 'react';

const Nav = (props)=> {
    return(
    <nav>
        <div className="nav-wrapper">
        <ul id="nav-mobile" className="right">
            <li><a onClick={props.clickedHome}>Home</a></li>
            <li><a onClick={props.clickedAbout}>About</a></li>
            <li><a onClick={props.clickedProjects}>Projects</a></li>
            <li><a onClick={props.clickedContact}>Contact</a></li>
        </ul>
        </div>
    </nav>
    )
    
}


export default Nav;


