import React, {Component} from 'react';
import About from './about';
import Projects from './projects';
import Contact from './contact';
import Main from './Main';
class AboutSection extends React.Component {

  render(){
    if (this.props.page == 'about'){
      return(
          <About 
          clickedAbout = {this.props.clickedAbout}
          clickedProjects = {this.props.clickedProjects}
          clickedContact = {this.props.clickedContact}
          />       
      )
    }else if (this.props.page =='projects'){
      return(
        <Projects 
        clickedAbout = {this.props.clickedAbout}
        clickedProjects = {this.props.clickedProjects}
        clickedContact = {this.props.clickedContact}
        />
      )
    }else if (this.props.page == 'contact'){
      return(
        <Contact />
      )
    }else if (this.props.page == 'home'){
      return(
        <Main clickedAbout = {this.props.clickedAbout}/>
      )
    }
  }
}
export default AboutSection;



